.checkoutItem {
	height: 100px;
	background-color: white;
	box-shadow: rgb(0 0 0 / 4%) 0 7px 8px 0;
	margin-top: 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0px 20px;
	&__info {
		display: flex;
		align-items: center;
		flex-direction: row;

		flex: 0.3;

		h4 {
			margin-top: 10px;
			font-size: 18px;
		}
		> p {
			color: gray;
			font-size: 15px;
			margin-top: 20px;
			margin-right: 20px;
		}
	}
	&__unitCost {
		color: gray;
		font-size: 13px;
	}
	&__totalCost {
		color: gray;
		font-weight: bold;
		font-size: 14px;
		margin-right: 20px;
	}
	&__buyInfo {
		flex: 0.3;
		display: flex;
		align-items: center;
		justify-content: space-between;
		> input {
			width: 100px;
			margin-right: 20px;
		}
	}
}

@media screen and (max-width: 650px) {
	.checkoutItem {
		&__info {
			h4 {
				font-size: 15px;
			}
			p {
				font-size: 12px;
				// margin-top: inherit;
			}
		}
	}
}
