.feed {
	width: 100%;
	flex: 0.72;
	height: auto;
	border-radius: 3px;
	box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
	background-color: white;
	display: flex;
	align-items: center;
	flex-direction: column;
	// justify-content: flex-st;
	flex-wrap: wrap;
	padding-left: 15px;
	padding-bottom: 20px;
	margin-top: 10px;
	&__header {
		width: 100%;
		display: flex;
		padding: 30px 30px;
		justify-content: space-between;
		align-items: center;
	}
	&__category {
		flex: 0.4;
		display: flex;
		align-items: center;
		justify-content: space-around;
		> label {
			margin-left: 10px;
		}
	}
	&__content {
		width: 96%;
		min-height: 500px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__products {
		width: 100%;
		min-height: 500px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
	&__productsList {
		height: auto;
		width: 100%;
		min-height: 500px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		margin-bottom: 20px;
	}
}

@media screen and (max-width: 960px) {
	.feed {
		&__category {
			flex-direction: column;
			label {
				margin-left: 0px;
			}
		}
	}
}
